import React from 'react'
import '../newsCard/newsCards.css';

const NewsCard = ({newsItems}) => {
    
  return (
    <div className='newsCard'>
    <img className='newsImage' alt={newsItems.title} src={newsItems.urlToImage ? newsItems.urlToImage : "https://st3.depositphotos.com/23594922/31822/v/1600/depositphotos_318221368-stock-illustration-missing-picture-page-for-website.jpg" }/>
    <div className='newsText'>
    <div className='title'>
    <span className='newsTitle'>{newsItems.title}</span><br/>
    <span className='author'>
    <a className='source' href={newsItems.url} target='_blank'>
    <b>Short </b>
    </a>
    <span className='muted'>
    by {newsItems.author ? newsItems.author : "unknown"}
    </span>
    </span>
    </div>
    <div className='lowerText'>
    <div className='description'>{newsItems.description}</div>
    <span className='readMore'>read more at {''} 
    <a className='source' href={newsItems.url} target='_blank'>
    <b>{newsItems.source.name}</b>
    </a>
    </span>
    </div>
    </div>
    </div>
  )
}

export default NewsCard;