import React from 'react'
import '../components/navStyle.css';
import HamburgerMenu from '../components/hamburgerMenu'


const NavInshort = ({setCategory}) => {
  return (
    <div className='navBar'>
     <div className='hamBurgerDrawer'><HamburgerMenu setCategory={setCategory}/></div>
     <img className='navLogo' src='https://assets.inshorts.com/website_assets/images/logo_inshorts.png'/>
    </div>
  )
}

export default NavInshort