import { useEffect, useState } from 'react';
import './App.css';
import NavInshort from './components/navInshort';
import NewsContent from './NewsContent/NewsContent';
import axios from 'axios'
import Footer from './components/footer';



function App() {
  
  const [category,setCategory] = useState('General');
  const [newsArray,setNewsArray] = useState([]);
  const [newsResult,setNewsResult] = useState();
  const [loadMore,setLoadMore] = useState(20)

  const newsApi = async()=>{
    try{
      const proxyUrl = "https://cors-anywhere.herokuapp.com";
      const news = await axios.get(`https://newsapi.org/v2/top-headlines?country=in&apiKey=4653e9f8ff5e436eb6bf7a537118ecd2&pageSize=${loadMore}`)
      setNewsArray(news.data.articles);
      setNewsResult(news.data.totalResults);
    }
    catch(error){
      console.log(error)
    }
  }



  useEffect(()=>{
    newsApi()
  },[newsResult,category,loadMore])

  return (
    <div className="App">
      <NavInshort setCategory={setCategory}/>
      <NewsContent loadMore={loadMore} setLoadMore={setLoadMore} newsArray={newsArray} newsResult={newsResult}/>
      <Footer/>
    </div>
  );
}

export default App;
