import React from 'react'
import Container from '@material-ui/core/Container';
import '../NewsContent/NewsContent.css';
import NewsCard from '../components/newsCard/newsCard';

const NewsContent = ({newsArray,newsResult,loadMore,setLoadMore}) => {
  return (
    <Container maxWidth='md'>
    <div className='content'>
    <div className='downloadMessage'>
    <span className='downloadText'>For the best experience use inshorts app on your smartphone</span>
    <img height="80%" alt='app_store' src='https://assets.inshorts.com/website_assets/images/appstore.png'/>
    <img height="80%" alt='play_store' src='https://assets.inshorts.com/website_assets/images/playstore.png'/>
    </div>

    {
      newsArray.map((newsItems)=>(
        <NewsCard newsItems={newsItems} key={newsItems.title}/>
      ))
    }

    {
      loadMore <= newsResult && (
        <>
        <hr/>
        <button onClick={()=>setLoadMore(loadMore + 20)} className='loadMore'>Load More</button>
        </>
        
      )
    }

    
    </div>
    </Container>
  )
}

export default NewsContent